
import BasePanel from '@/components/BasePanel.vue'
import { Attempt, User } from '@/types'
import { defineComponent } from 'vue'
import dayjs from 'dayjs'
import relativeTime from 'dayjs/plugin/relativeTime'
import ActivityRow from '@/components/ActivityRow.vue'
import UserPageStat from '@/components/UserPageStat.vue'
dayjs.extend(relativeTime)

export default defineComponent({
  name: 'UserPage',
  components: { BasePanel, ActivityRow, UserPageStat },
  props: {
    userId: {
      type: String,
      required: true
    }
  },
  computed: {
    myAccount (): boolean {
      return this.$store.state.user && this.$store.state.user.id === this.userId
    },
    user (): User {
      return this.$store.state.user
    },
    attempts (): Attempt[] {
      return this.$store.getters.attemptsByUser(this.userId)
    },
    sortedAttempts (): Attempt[] {
      return [...this.attempts].sort((a, b) => {
        return new Date(b.endDate).valueOf() - new Date(a.endDate).valueOf()
      })
    },
    completedAttempts (): Attempt[] {
      return this.sortedAttempts.filter(attempt => {
        console.log(attempt)
        return attempt.status === 'completed'
      })
    },
    completedCourses (): number {
      return this.user.courses.reduce((a, courseId) => a + (this.$store.getters.courseCompletedPercentage(courseId, this.userId) === 100 ? 1 : 0), 0)
    }
  }
})
